import Immutable from 'immutable';
import { RebatesInfo } from '../../../utilities/src/rebates_const';
// Important
// We get data from here

const getInitialState = () => {
  let stateobj = {};
  stateobj.rebates_info = new RebatesInfo();
  stateobj.rebates_close_popup = [];
  stateobj.rebates_activated_sites = [];
  stateobj.coupon_keyword = "";
  stateobj.rebates_auto_activation_info = [];
  stateobj.userInfo = {
    your_name: null,
    yourUsablePoint: null,
    your_point_tmp: null,
    futureGrantedPoint: null,
    rank: null
  };
  return stateobj;
};

export default (state, action) => {
  state = Immutable.fromJS(state ? state : getInitialState());
  let value = "";
  let cur_data ="";
  switch (action.type) {
    case 'SET_KEY_WITH_DATA':
      return state.set(action.data.key, action.data.value).toJS();
    case 'ADD_HISTORY':
      value = (action.data.local?state.get('history').slice(-5).unshift(action.data.input):state.get('history').slice(-5).push(action.data.input));
      return state.set('history', value).toJS();
    case 'CLEAR_HISTORY':
      value = state.get('history').clear();
      return state.set('history', value).toJS();
    case 'CLEAR_SINGLE_HISTORY':
      value = state.get('history').filter((item) => { return item !== action.data.input });
      return state.set('history', value).toJS();
    case 'SET_UUID':
      return state.set('uuid', action.data).toJS();
    case 'SET_TBUID':
      return state.set('tbuid', action.data).toJS();
    case 'SET_CLEAR_HISTORY_FLAG':
      return state.set('clearHistory', action.data).toJS();
    case 'SET_REBATES_INFO':
      return state.set('rebates_info', action.data).toJS();
    case 'ADD_REBATES_POPUP_CLOSE_SITE':
      value = state.get('rebates_close_popup').push(action.data);
      return state.set('rebates_close_popup', value).toJS();
    case 'REMOVE_REBATES_POPUP_CLOSE_SITE':
      cur_data = state.get('rebates_close_popup');
      value = cur_data.filter((item) => { return item !== action.data });
      return state.set('rebates_close_popup', value).toJS();
    case 'CLEAR_REBATES_POPUP_CLOSE_SITE':
      value = state.get('rebates_close_popup').clear();
      return state.set('rebates_close_popup', value).toJS();
    case 'ADD_ACTIVATED_SITE':
      value = state.get('rebates_activated_sites').push(action.data);
      return state.set('rebates_activated_sites', value).toJS();
    case 'UPDATE_ACTIVATED_SITE':
      cur_data = state.get('rebates_activated_sites');
      value = cur_data.map(item => item.get("domain") == action.data.domain ? action.data : item);
      return state.set('rebates_activated_sites', value).toJS();
    case 'REMOVE_EXPIRED_ACTIVATED_SITE':
      cur_data = state.get('rebates_activated_sites');
      value = cur_data.filter((item) => { return item.get('domain') !== action.data.domain });
      return state.set('rebates_activated_sites', value).toJS();
    case 'CLEAR_ACTIVATED_SITE':
      value = state.get('rebates_activated_sites').clear();
      return state.set('rebates_activated_sites', value).toJS();
    case 'SET_RBAR_ACTIVE':
      return state.set('rakutenBarInfo_active', action.data).toJS();
    case 'SET_RBAR_SEND_NUMBER':
      return state.set('rakutenBarInfo_send_number', action.data).toJS();
    case 'SET_RBAR_ACCESSURL':
      return state.set('rakutenBarInfo_accessurl', action.data).toJS();
    case 'SET_RBAR_DOMAIN':
      return state.set('rakutenBarInfo_domain', action.data).toJS();
    case 'SET_RBAR_USERAGENT':
      return state.set('rakutenBarInfo_userAgent', action.data).toJS();
    case 'SET_RBAR_OSVERSION':
      return state.set('rakutenBarInfo_osVersion', action.data).toJS();
    case 'SET_RBAR_RZ_COOKIE':
      return state.set('rakutenBarInfo_cookie', action.data).toJS();
    case 'SET_URLCHECK':
      return state.set('URLCheck', action.data).toJS();
    case 'SET_RAKUTEN_ASDEFAULT':
      return state.set('isRakutenAsDefault', action.data).toJS();
    case 'CLEAR_RAKUTEN_ASDEFAULT':
      value = undefined;
      return state.set('isRakutenAsDefault', value).toJS();
    case 'SET_MESSAGEREAD_TIME':
      return state.set('messageReadTime', action.data).toJS();
    case 'SET_YAMAKAWE':
      return state.set('yamawakeInfo', action.data).toJS();
    case 'SET_KUCHISU':
      return state.set('kuchisuCount', action.data).toJS();
    case 'SET_LOGINCHECK_TIME':
      return state.set('loginCheckTime', action.data).toJS();
    case 'SET_LOGIN':
      return state.set('login', action.data).toJS();
    case 'SET_USER_INFO':
      return state.set('userInfo', action.data).toJS();
    case 'SET_CALLLOAD':
      return state.set('callLoad', action.data).toJS();
    case 'CLEAR_CALLLOAD':
      value = undefined;
      return state.set('callLoad', value).toJS();
    case 'SET_AB_TEST_SETTINGS':
      return state.set('abTestSettings', action.data).toJS();
    case 'CLEAR_AB_TEST_SETTINGS':
      value = state.get('abTestSettings').clear();
      return state.set('abTestSettings', value).toJS();
    case 'SET_EXTENSION_SETTING':
      return state.set('ExtensionSetting', action.data).toJS();
    case 'SET_SEARCHBAR_SETTING':
      return state.set('SearchBarSetting', action.data).toJS();
    case 'SET_PICKUP_BANNER_DISABLED_TIME':
      return state.set('pickup_banner_disabled_time', action.data).toJS();
    case 'CLEAR_PICKUP_BANNER_DISABLED_TIME':
      value = 0;
      return state.set('pickup_banner_disabled_time', value).toJS();

    /*Not stored permanantly*/
    case 'SET_REBATES_CAMPAIGN':
      return state.set('rebates_campaignInfo', action.data).toJS();
    case 'SET_COUPON_KEYWORD':
      return state.set('coupon_keyword', action.data).toJS();
    case 'SET_REBATES_WORKFLOW_STATE':
      return state.set('rebates_workflowState', action.data).toJS();
    case 'SET_CID':
      return state.set('cid', action.data).toJS();
    case 'SET_RAKUTEN_BAR_INFO_CONFIG_UPDATE_TIME':
      return state.set('rakutenBarInfoConfig_update_time', action.data).toJS();
    case 'ADD_REBATES_CAMPAIGN_HOVER_STATE':
      value = state.get('rebates_campaign_hover_states').push(action.data);
      return state.set('rebates_campaign_hover_states', value).toJS();
    case 'UPDATE_REBATES_CAMPAIGN_HOVER_STATE':
      cur_data = state.get('rebates_campaign_hover_states');
      value = cur_data.map(item => item.get("_domainName") == action.data._domainName ? action.data : item);
      return state.set('rebates_campaign_hover_states', value).toJS();
    case 'REMOVE_REBATES_CAMPAIGN_HOVER_STATE':
      cur_data = state.get('rebates_campaign_hover_states');
      value = cur_data.filter(item => item.get("_domainName") != action.data);
      return state.set('rebates_campaign_hover_states', value).toJS();
    case 'CLEAR_REBATES_CAMPAIGN_HOVER_STATES':
      value = state.get('rebates_campaign_hover_states').clear();
      return state.set('rebates_campaign_hover_states', value).toJS();
    case 'UPDATE_REBATES_AUTO_ACTIVATION':
        cur_data = state.get('rebates_auto_activation_info');
        value = cur_data.map(item => item.get("tab_id") != action.data.tab_id ? item : action.data);
        return state.set('rebates_auto_activation_info', value).toJS();
    case 'ADD_REBATES_AUTO_ACTIVATION':
          value = state.get('rebates_auto_activation_info').push(action.data);
          return state.set('rebates_auto_activation_info', value).toJS();
    case 'CLEAR_REBATES_AUTO_ACTIVATION':
          cur_data = state.get('rebates_auto_activation_info');
          value = cur_data.filter(item => item.get("tab_id") !== action.data);
          return state.set('rebates_auto_activation_info', value).toJS();
    case 'SET_REBATE_ADDITIONAL_DISCOUNT_TARGET_INFO':
      return state.set('rebates_additional_discount_target_info', action.data).toJS();
    default:
      return state.toJS();
  }
};
