import { store} from '../background_data_store';
import { PICKUP_BAR_PRODUCT_BASE_URL,
        PICKUP_BAR_SPU_URL
    } from '../../../utilities/src/pickup_const';
import DataBackend from '../factory/data_backend'
export default class IchibaDataBackend extends DataBackend {

    constructor() {
        super();
    }

    //Using default implementation
    //async _fetchServerJson(_url) { }

    async fetchProduct(_page_url) {
        const _url = PICKUP_BAR_PRODUCT_BASE_URL +
            "?tuid=" + store.getState().data.tbuid +
            "&tool_id=1" +
            "&version=" + platform_api_root.runtime.getManifest().version +
            "&target_url=" + _page_url;
        const _product = await this._fetchServerJson(_url);
        return _product;
    }

    async fetchSPU() {
        return await this._fetchServerJson(PICKUP_BAR_SPU_URL);
    }
}