// Flag to indicate if the data is loaded from storage
// Reason to have this flag is the data loading is now async (due to Storage API)
// some dispatches may happen before the data are loaded, and those dispatches
// can trigger the data saving to Storage API, which will cause the data to be
// saved before it is loaded and will lead to data loss

export const globalState = {
  isDataLoadedFromStorage: false
}
