import * as txml from 'txml';

function _getElementsByTagName(tagName) {
    const elements = [];
    if (this.tagName === tagName) {
        elements.push(this);
    }
    function getElementsByTagNameRecursive(node) {
        if (typeof node !== 'object') {
            return;
        }
        if (node.children) {
            for (let i = 0; i < node.children.length; i++) {
                const child = node.children[i];
                if (child.tagName === tagName) {
                    elements.push(child);
                }
                getElementsByTagNameRecursive(child);
            }
        }
    }
    getElementsByTagNameRecursive(this);
    return elements;
}

function _getAttribute(name) {
    if (this.attributes) {
        return this.attributes[name] || "";
    }
    return "";
}

function _initiateBindsToDomTree(root) {
    function bindRecursive(node) {
        if (typeof node !== 'object') {
            return;
        }
        node.getElementsByTagName = _getElementsByTagName.bind(node);
        node.getAttribute = _getAttribute.bind(node);

        // Because the parsed structure of txml is different from browser native DOMParser
        // So we need to restructure textContent data from the raw txml parsed object
        function getTextContentRecursive(node) {
            if (typeof node === 'string') {
                return node;
            }
            if (node.children) {
                let textContent = "";
                for (let i = 0; i < node.children.length; i++) {
                    const child = node.children[i];
                    if (typeof child === 'string') {
                        textContent += child;
                    } else {
                        textContent += getTextContentRecursive(child);
                    }
                }
                return textContent;
            }
            return "";
        }
        node.textContent = getTextContentRecursive(node);

        if (node.children) {
            for (let i = 0; i < node.children.length; i++) {
                const child = node.children[i];
                bindRecursive(child);
            }
        }
    }
    bindRecursive(root);
}

export default class XmlParser{
    constructor(response){
        this.xmlResponse = response.trim();
        this.xmlDoc = null;
    }

    getXmlDocument(){
        if(this.xmlDoc == null){
            this.xmlResponse = this.replaceNameSpace("http://www.mozilla.org/keymaster/gatekeeper/there.is.only.xul");
            // Change to use txml parser (https://github.com/tobiasnickel/tXml) because web worker are not supporting DOMParser anymore
            // 1.6kb minified + gzipped, parsing at average the same speed as native DOMParser
            // Other DOMParser cannot work in the browser environment (for example: jsdom), because they only target node environment
            const parsed = txml.parse(this.xmlResponse);
            // When returned XML text has redundant <?xml version="1.0" encoding="UTF-8"?> tag, skip it
            this.xmlDoc = parsed.length > 1 ? parsed[1] : parsed[0];
            // Tradeoffs: txml only parse XML to json, it's not providing operating function such as getElementsByTagName or getAttribute,
            // So we need to bind these functions to the json object
            _initiateBindsToDomTree(this.xmlDoc);
        }
        return this.xmlDoc;
    }

    replaceNameSpace(searchString){
        let searchPos = this.xmlResponse.indexOf(searchString);
        if(searchPos >0){
            return this.xmlResponse.replace(searchString, "");
        }
        return this.xmlResponse;
    }

    getAttributeValueByTagName(tagName, attribute){
        return this.getXmlDocument().getElementsByTagName(tagName)[0].getAttribute(attribute);
    }

    getElementsByTagName(tagName){
        return this.getXmlDocument().getElementsByTagName(tagName)[0];
    }
}
