import {DEFAULT_DOMAIN_ENTITIES_URL, CAMPAIGN_INFO_URL, COUPON_STORE_INFO_URL, REBATES_DOMAIN_FULL_ADDRESS, AFFILIATE_LINKS_URL,
    REBATES_WORKFLOW_STATE, REBATES_MATCH_PATTERN, RAKUTEN_LOGIN_PATTERN, RAKUTEN_LOGIN_PATTERN_OMNI,
    RebatesInfo, REBATES_TOOLBAR_CAMPAIGN_URL,
    REBATES_CAMPAIGN_HOVER_EXPIRATION_TIME_LIMIT, REBATES_CAMPAIGN_HOVER_VISIBILITY_COUNT,
    isActivatedPageAlreadyExpired, MEMBER_DETAILS_URL, REBATES_MEMBER_DETAILS_INTERVAL, REBATE_ADDITIONAL_DISCOUNT_TARGET_INFO
} from '../../../utilities/src/rebates_const';
import {BANNER_DISABLE_OPTION, CONST_RAKUTEN_WEBSEARCH} from '../../../utilities/src/setting_config';
import {findMatchPattern, sendMessageByPort} from '../../../utilities/src/common_helper';
/**
* Class that represents the domain entity details for cashback
*/
export class DomainEntities {
constructor() {
    this.shoppingURL = '';
    this.activationCode = 0;
    this.domainMatchPatterns = '';
    this.cashBackText = '';
    this.fixedCashBack = true;
    this.urlName = '';
}

getShoppingURL() {
    return this.shoppingURL;
}

getActivationCode() {
    return this.activationCode;
}

getMatchPattern() {
    return this.domainMatchPatterns;
}

getCashBackText() {
    return this.cashBackText;
}

getFixedCashBack() {
    return this.fixedCashBack;
}

getUrlName() {
    return this.urlName;
}
}

/**Parent class which contains all the info related to Rebates campaign */
export class CampaignList{
constructor(){
    this.campaignId=0,
    this.campaignLink = '';
    this.campaignName = '';
    this.campaignType = '';
    this.campaignStartDate = new Date();
    this.campaignCoupons = [];
    this.couponStores = [];
}

}

