export default class Rebates {
constructor() {
    this.domainentities = [];
    this.campaignList = {};
    this.metaData = "";
    this.rzCookie = "";
    this.affiliateLinks = [];
    this.paidLinkPatterns = [];
    this.domainListURL = DEFAULT_DOMAIN_ENTITIES_URL;
}

async initRebatesConfig(_store) {
    await this.fetchMetaTag(_store).then((result) => {
        if (result) {
            this.metaData = result;
        }
    });
    this.getRebatesAdditionalDiscountTargetInfo(_store).then(result => {
        this.getMerchantDomainEntities(_store, result);
    });
    await this.getAffiliateLinksList();
}

async init(_store){
    this.initRebatesConfig(_store);
    _store.dispatch({type: 'SET_REBATES_WORKFLOW_STATE', data: {} });

    platform_api_root.alarms.create("DomainEntityInterval", {
        periodInMinutes: 15
    });
    platform_api_root.alarms.onAlarm.addListener((alarm) => {
        if (alarm.name === "DomainEntityInterval") {
            this.getRebatesAdditionalDiscountTargetInfo(_store).then(result => {
                this.getMerchantDomainEntities(_store, result);
            });
        }
    });

    // Updated Every 24 Hours
    platform_api_root.alarms.create("RebatesAffiliateLinksInterval", {
        periodInMinutes: 1440
    });
    platform_api_root.alarms.onAlarm.addListener((alarm) => {
        if (alarm.name === "RebatesAffiliateLinksInterval") {
            this.getAffiliateLinksList();
        }
    });
}

/**Get currently running Rebates campaign information */
async getCampaignLists(){
    let response = await fetch(CAMPAIGN_INFO_URL);
    if (response && response.status === 200) {
        let rebatesResponse = await response.json();
        if (rebatesResponse != null && rebatesResponse.campaigns.length > 0) {
            let campaign = new CampaignList();
            let stores = [];
            campaign.campaignId = rebatesResponse.campaigns[0].id,
                campaign.campaignLink = rebatesResponse.campaigns[0].link;
            campaign.campaignName = rebatesResponse.campaigns[0].name;
            campaign.campaignType = rebatesResponse.campaigns[0].type;
            campaign.campaignStartDate = rebatesResponse.campaigns[0].start != null ? new Date(rebatesResponse.campaigns[0].start * 1000) : new Date();
            if (rebatesResponse.campaigns[0].coupons != null && rebatesResponse.campaigns[0].coupons.length > 0) {
                //Get random selection of 3 campaigns out of all the list
                let selectionList = this.getRandomSelection(rebatesResponse.campaigns[0].coupons.length);
                for (let i = 0; i < selectionList.length; i++) {
                    try {
                        let selectedCoupon = new CampaignCoupon();
                        if (selectionList[i] - 1 >= 0) {
                            let sel = selectionList[i] - 1;
                            selectedCoupon.description = rebatesResponse.campaigns[0].coupons[sel].description;
                            selectedCoupon.expires = rebatesResponse.campaigns[0].coupons[sel].expires;
                            selectedCoupon.id = rebatesResponse.campaigns[0].coupons[sel].id;
                            selectedCoupon.postedDate = new Date(rebatesResponse.campaigns[0].coupons[sel].postedDate * 100);
                            selectedCoupon.storeId = rebatesResponse.campaigns[0].coupons[sel].storeId;
                            selectedCoupon.featured = rebatesResponse.campaigns[0].coupons[sel].featured;
                            selectedCoupon.type = rebatesResponse.campaigns[0].coupons[sel].type;
                            selectedCoupon.typeId = rebatesResponse.campaigns[0].coupons[sel].typeId;
                            campaign.campaignCoupons.push(selectedCoupon);
                            stores.push(selectedCoupon.storeId);
                        }
                    }
                    catch (e) {
                        //Ignore and continue loop
                    }
                }
                this.campaignList = campaign;
            }
            if (stores.length > 0) {
                await this.getCouponStoreList(stores);
                return true;
            }
        }
    }
    return false;
}

/**Get store information participating in Rebates campaign */
async getCouponStoreList(stores){
    let response = await fetch(COUPON_STORE_INFO_URL + stores.toString());
    if (response && response.status === 200) {
        let storeResponse = await response.json();
        if (storeResponse != null && storeResponse.stores.length > 0) {
            for (let i = 0; i < storeResponse.stores.length; i++) {
                try {
                    let storeInfo = new CouponStore();
                    storeInfo.storeName = storeResponse.stores[i].attributes == null ? '' : storeResponse.stores[i].attributes.storeName_en_JP;
                    storeInfo.jp_ktn_storeName = storeResponse.stores[i].attributes == null ? '' : storeResponse.stores[i].attributes.storeName_katakana_JP;
                    storeInfo.jp_hgn_storeName = storeResponse.stores[i].attributes == null ? '' : storeResponse.stores[i].attributes.storeName_hiragana_JP;
                    storeInfo.smallLogo = storeResponse.stores[i].smallLogo;
                    storeInfo.cashBackText = storeResponse.stores[i].cashBackText;
                    storeInfo.description = storeResponse.stores[i].description;
                    storeInfo.id = storeResponse.stores[i].id;
                    storeInfo.urlName = storeResponse.stores[i].urlName;
                    if (storeResponse.stores[i].totalReward != null && storeResponse.stores[i].totalReward.range) {
                        storeInfo.isFixedCashBack = false;
                        storeInfo.cashBack = isNaN(storeResponse.stores[i].totalReward.range.high) ? 0 : storeResponse.stores[i].totalReward.range.high;
                    }
                    else if (storeResponse.stores[i].totalReward != null && storeResponse.stores[i].totalReward.amount) {
                        storeInfo.isFixedCashBack = true;
                        storeInfo.cashBack = isNaN(storeResponse.stores[i].totalReward.amount) ? 0 : storeResponse.stores[i].totalReward.amount;
                    }
                    else {
                        storeInfo.isFixedCashBack = true;
                        storeInfo.cashBack = 0;
                    }

                    this.campaignList.couponStores.push(storeInfo);
                }
                catch (e) {
                    //In case of error ignore and continue loop
                }
            }
            return true;
        }
    }
    return false;
}

getDomainEntities() {
    fetch(this.domainListURL)
        .then((response) => response.json())
        .then((data) => {
            if (data != undefined) {
                //clean the domain entities only when the API call is success
                this.domainentities = [];
                let rebatesResponse = data;
                let shoppingCount = rebatesResponse.total;
                let domainEntities = rebatesResponse.domains;
                for (let shopper = 0; shopper < shoppingCount; shopper++) {
                    domainEntities[shopper].stores.map(store => {
                        this.rebatesStoresEntry(store);
                    });
                }
            }
        });
}

getMerchantDomainEntities(_store, target_info) {
    //when the user update the extension
    //need to check whether this user is a target user or not
    _store.dispatch({
        type: "SET_REBATE_ADDITIONAL_DISCOUNT_TARGET_INFO",
        data: target_info
    });

    if (target_info.is_targeted) {
        this.domainListURL = DEFAULT_DOMAIN_ENTITIES_URL + `&targetSet=${target_info.target_group}`;
    }else{
        this.domainListURL = DEFAULT_DOMAIN_ENTITIES_URL;
    }

    this.getDomainEntities();
}

    getRebatesAdditionalDiscountTargetInfo(_store) {
        return new Promise((resolve) => {
            let rebates_additional_discount_target_info = _store.getState().data.rebates_additional_discount_target_info;
            let time_of_now = new Date().getTime();
            platform_api_root.cookies.get({ "url": `${CONST_RAKUTEN_WEBSEARCH.defaultUrl}`, "name": "Rz" }, cookie => {
                if (this.metaData && cookie != undefined) {
                    if (rebates_additional_discount_target_info == undefined ||
                        rebates_additional_discount_target_info.expire_time < time_of_now) {
                        fetch(MEMBER_DETAILS_URL, {
                            headers: {
                                'token': this.metaData
                            }})
                            .then((response) => response.json())
                            .then((data) => {
                                let member_details = data;
                                let rebates_additional_discount_target_info = new REBATE_ADDITIONAL_DISCOUNT_TARGET_INFO();
                                rebates_additional_discount_target_info.expire_time = new Date().getTime() + REBATES_MEMBER_DETAILS_INTERVAL;
                                if (member_details.targetSet) {
                                    rebates_additional_discount_target_info.is_targeted = true;
                                    rebates_additional_discount_target_info.target_group = member_details.targetSet;
                                }
                                resolve(rebates_additional_discount_target_info);
                            });
                    } else {
                        resolve(rebates_additional_discount_target_info);
                    }
                } else {
                    if (rebates_additional_discount_target_info.is_targeted) {
                        rebates_additional_discount_target_info.is_targeted = false;
                        rebates_additional_discount_target_info.target_group = '';
                    }

                    if (rebates_additional_discount_target_info.expire_time == 0) {
                        rebates_additional_discount_target_info.expire_time = time_of_now + REBATES_MEMBER_DETAILS_INTERVAL;
                    }
                    resolve(rebates_additional_discount_target_info);
                }
            });
        });
    }

rebatesLoginStatusChanged(newVal, oldVal, _store) {
    if(newVal == 1 && oldVal == 0){
        if(this.metaData){
            fetch(MEMBER_DETAILS_URL, {
                headers: {
                    'token': this.metaData
                }})
            .then((response) => response.json())
            .then((data) => {
                let member_details = data;
                let rebates_additional_discount_target_info = new REBATE_ADDITIONAL_DISCOUNT_TARGET_INFO();
                rebates_additional_discount_target_info.expire_time = new Date().getTime() + REBATES_MEMBER_DETAILS_INTERVAL;
                if (member_details.targetSet) {
                    rebates_additional_discount_target_info.is_targeted = true;
                    rebates_additional_discount_target_info.target_group = member_details.targetSet;
                }
                this.getMerchantDomainEntities(_store, rebates_additional_discount_target_info);
            });
        }else{
            this.getMerchantDomainEntities(_store, new REBATE_ADDITIONAL_DISCOUNT_TARGET_INFO());
        }
    }

    if (newVal == 0 && oldVal == 1) {//when logout
        this.metaData = '';
        this.domainListURL = DEFAULT_DOMAIN_ENTITIES_URL;
        //clear rebates info
        _store.dispatch({
            type:"SET_REBATES_INFO",
            data: new RebatesInfo()
        })
        //clear rebates cookies
        platform_api_root.cookies.remove({
            url: REBATES_DOMAIN_FULL_ADDRESS,
            name: "euid"
        });
        platform_api_root.cookies.remove({
            url: REBATES_DOMAIN_FULL_ADDRESS,
            name: "REQCOLID"
        });

        this.getMerchantDomainEntities(_store, new REBATE_ADDITIONAL_DISCOUNT_TARGET_INFO());
    }
}

/**
 * Saves stores to Array
 *
 *
 * @param domainEntities : Json root element
 * @param i : domainEntity iterator index
 * @param index : store element iterator index
 */
rebatesStoresEntry(stores) {
    try {
        let de = new DomainEntities();
        de.shoppingURL = stores.shoppingURL;
        de.domainMatchPatterns = stores.matchPattern;
        de.urlName = stores.urlName===null?'':stores.urlName;
        // As for cashbacktext, we cannot utilize their english text because rebates are for japanese.
        // So I will create text here from total reward.
        // de.cashBackText = domainEntities[i].stores[0].cashBackText;
        let amount = stores.totalReward.amount;
        let display = stores.totalReward.display;
        let reward = stores.reward;
        let percent = 0.00;

        // If amount is not there and range is there, we need to put range.high as amount, and use max as japanese.
        let max = '';
        if (reward != undefined) {
            if (reward.range != undefined) {
                display = reward.display;
                if (reward.range != undefined &&
                    reward.range.high != undefined) {
                    amount = reward.range.high;
                    max = '';
                    de.fixedCashBack = false;
                }
            }
        }

        if (display == 'Percentage') {
            percent = (amount * 1000) * 100 / 1000;
        }
        de.cashBackText = max + percent.toString() + '%';
        de.activationCode = stores.activationCode;
        this.domainentities.push(de);
    } catch (e) {
    }
}

escapeRegExp(text) {
    return text.replace(/[-[\]{}()*+?.,\\^$|#\s]/g, '\\$&');
}

async getAffiliateLinksList() {
    let response = await fetch(AFFILIATE_LINKS_URL);
    if (response && response.status === 200) {
        let affiliateResponse = await response.json();
        if (affiliateResponse != null && affiliateResponse.paidLinks.length > 0) {
            let temp_affiliate_links = [];
            for (let i = 0; i < affiliateResponse.paidLinks.length; i++) {
                try {
                    temp_affiliate_links.push(this.escapeRegExp(affiliateResponse.paidLinks[i]));
                }
                catch (e) {
                    //In case of error ignore and continue loop
                }
            }
            //reset the affiliate links only when the API call is success
            this.affiliateLinks = temp_affiliate_links;
            // This is additional regex for paidlinks, and need to use raw regex.
            let temp_affiliate_links_patterns = [];
            if (affiliateResponse.paidLinkPatterns != null) {
                if (affiliateResponse.paidLinkPatterns.length > 0) {
                    for (let i = 0; i < affiliateResponse.paidLinkPatterns.length; i++) {
                        try {
                            temp_affiliate_links_patterns.push(affiliateResponse.paidLinkPatterns[i]);
                        }
                        catch (e) {
                            //In case of error ignore and continue loop
                        }
                    }
                }
            }
            this.paidLinkPatterns = temp_affiliate_links_patterns;
        }
    }
}

generateRebatesInfo(url) {
    const url_domain = url.split(/[?#]/)[0];
    let rebates_info = new RebatesInfo();
    for (let de of this.domainentities) {
        try {
            if (url_domain.match(de.domainMatchPatterns)) {
                rebates_info.setLink(this.linkGenerator(de));
                rebates_info.setComment(de.cashBackText);
                rebates_info.setDomainRegex(de.domainMatchPatterns);
                rebates_info.setTokenAvailable(this.isTokenExisting());
                rebates_info.setFixedCashBack(de.fixedCashBack);
                rebates_info.setUrlName(de.urlName);
                break;
            }
        } catch (error) {
            console.log(error);
        }
    }
    return rebates_info;
}

linkGenerator(de) {
    let link = `${REBATES_DOMAIN_FULL_ADDRESS}/apis/v1${de.shoppingURL}?s=toolbar&interstitial=true&channelId=2&apiautologintoken=${this.metaData}`;
    return link;
}

async checkMetaTagIfURLGoesTOPPage(url, rzCookie, _store) {
    const logged_in = (_store.getState().data.login === "1");
    if (logged_in == false) { return; }
    let rebates_info = this.generateRebatesInfo(url);
    if (rzCookie == this.rzCookie && this.isTokenExisting()) {
        _store.dispatch({
            type: 'SET_REBATES_INFO',
            data: rebates_info
        });
    } else {
        if (url.indexOf(REBATES_DOMAIN_FULL_ADDRESS) != -1) {
            this.rzCookie = rzCookie;
            if (this.metaData == undefined ||
                this.metaData == "") {
                await this.fetchMetaTag(_store).then((result) => {
                    this.metaData = result;
                    _store.dispatch({type: 'SET_REBATES_INFO', data:rebates_info});
                });
            } else {
                _store.dispatch({type: 'SET_REBATES_INFO', data:rebates_info});
            }
        } else {
            _store.dispatch({
                type: 'SET_REBATES_INFO',
                data: rebates_info
            });
        }
    }
}

extractAllText(str){
    const re = /"(.*?)"/g;
    const result = [];
    let current = re.exec(str);
    while (current) {
      result.push(current.pop());
      current = re.exec(str);
    }
    return result.length > 0
      ? result
      : [str];
  }

    async fetchMetaTag(_store){
        return new Promise((resolve) => {
            platform_api_root.cookies.get({ "url": `${REBATES_DOMAIN_FULL_ADDRESS}`, "name": "euid" }, (cookie) => {
                if (cookie != undefined) {
                    this.metaData = cookie.value;
                    resolve(this.metaData);
                } else {
                    resolve();
                }
            });
        });
    }

isTokenExisting() {
    if (this.metaData) {
        return true;
    } else {
        return false;
    }
}

getRandomSelection(upperLimit){
    let indexes = [];
    if(upperLimit!=null && upperLimit != undefined){
        while(indexes.length <3){
            let i = Math.floor((Math.random() * upperLimit) + 1);
            if (indexes.indexOf(i) > -1) {
                continue;
            }
            indexes.push(i);
        }
    }
    return indexes;
}

findMatchedRebatesMerchantDomain(_url) {
    let got_pattern_match = false;
    let _domainEntities =  this.domainentities;
    for (let i in _domainEntities) {
        let r = new RegExp(_domainEntities[i].domainMatchPatterns);
        got_pattern_match = r.test(_url);
        if (got_pattern_match){
            break;
        }
    }
    return got_pattern_match;
}

mayShowRebatesPopUp(_tabId, _store){
    const currentWorkflowState = _store.getState().data.rebates_workflowState;
    const currentWorkflowStateForTab = (currentWorkflowState && currentWorkflowState[_tabId])?currentWorkflowState[_tabId]:REBATES_WORKFLOW_STATE.ON_DEFAULT;
    return currentWorkflowStateForTab != REBATES_WORKFLOW_STATE.ON_SUPPRESS_POPUP;
}

checkAndUpdateCampaignHoverState(campaign_hover_state, _store){
    if(campaign_hover_state._counter < REBATES_CAMPAIGN_HOVER_VISIBILITY_COUNT){
        return true;
    }

    const current_time = new Date().getTime();
    if (current_time - campaign_hover_state._lastUpdateTime < REBATES_CAMPAIGN_HOVER_EXPIRATION_TIME_LIMIT) {
        return false;
    }else{
        _store.dispatch({
            type: "REMOVE_REBATES_CAMPAIGN_HOVER_STATE",
            data: campaign_hover_state._domainName
        });
    }
    return true;
}

mayShowRebatesCampaignHover(_store, _url) {
    const host_domain = new URL(_url).hostname;
    let campaign_hover_state = _store.getState().data.rebates_campaign_hover_states.find(item => {
                                    return item._domainName == host_domain;
                                });
    if (campaign_hover_state != undefined
        && !this.checkAndUpdateCampaignHoverState(campaign_hover_state, _store)) {
            return false;
    }
    return true;
}

getCurrentStateForTab(_tabId, _store){
    const currentWorkflowState = _store.getState().data.rebates_workflowState;
    return (currentWorkflowState && currentWorkflowState[_tabId])?currentWorkflowState[_tabId]:REBATES_WORKFLOW_STATE.ON_DEFAULT;
}

setCurrentStateForTab(_tabId, _newState, _store){
    if (_store.getState().data !=undefined && _store.getState().data.rebates_workflowState != undefined) {
        const currentWorkflowState = _store.getState().data.rebates_workflowState;
        currentWorkflowState[_tabId] = _newState;
        _store.dispatch({type: 'SET_REBATES_WORKFLOW_STATE', data:currentWorkflowState});
    } else {
        _store.dispatch({type: 'SET_REBATES_WORKFLOW_STATE', data:{_tabId:0}});
    }
}

//_url (URL Being inspected), _tabId(Id of the tab), _store (Data Store)
checkUrlForSuppression(_url, _tabId, _store){
    const currentWorkflowState = this.getCurrentStateForTab(_tabId, _store);

    const isURLRebateLink   = findMatchPattern(_url, [REBATES_MATCH_PATTERN, RAKUTEN_LOGIN_PATTERN, RAKUTEN_LOGIN_PATTERN_OMNI]);
    const isURLAffiliateLink  = findMatchPattern(_url, this.affiliateLinks);
    const isURLStoreLink      = this.findMatchedRebatesMerchantDomain(_url);
    const isPaidLinkPatterns = findMatchPattern(_url, this.paidLinkPatterns);
    let newWorkflowState = this.checkNewWorkflowState(isURLRebateLink, isURLAffiliateLink, isURLStoreLink, currentWorkflowState, isPaidLinkPatterns);
    this.setCurrentStateForTab(_tabId, newWorkflowState, _store);
}

checkNewWorkflowState(isURLRebateLink, isURLAffiliateLink, isURLStoreLink, currentWorkflowState, isPaidLinkPatterns) {
    let newWorkflowState = REBATES_WORKFLOW_STATE.ON_DEFAULT;
    if (isURLRebateLink) {
        //If user lands on rebates.jp page
        newWorkflowState = REBATES_WORKFLOW_STATE.ON_REBATES;
    } else if (isURLAffiliateLink) {
        if (currentWorkflowState != REBATES_WORKFLOW_STATE.ON_REBATES &&
            currentWorkflowState != REBATES_WORKFLOW_STATE.ON_TRACK_URL) {
            newWorkflowState = REBATES_WORKFLOW_STATE.ON_AFFILIATE_FROM_OTHER;
        } else {
            newWorkflowState = REBATES_WORKFLOW_STATE.ON_TRACK_URL;
        }
    } else if (isURLStoreLink) {
        //If user lands on a store (e.g macys.com), then depending upon previous state we show or not show the popup
        if (currentWorkflowState == REBATES_WORKFLOW_STATE.ON_AFFILIATE_FROM_OTHER ||
            currentWorkflowState == REBATES_WORKFLOW_STATE.ON_SUPPRESS_POPUP ||
            isPaidLinkPatterns) {
            newWorkflowState = REBATES_WORKFLOW_STATE.ON_SUPPRESS_POPUP;
        }
    }
    return newWorkflowState;
}
checkAutoActivations(_url, _tabId, _store) {
    const isMatchTrackURL = findMatchPattern(_url, this.affiliateLinks);
    if (isMatchTrackURL) {
        const currentWorkflowState = this.getCurrentStateForTab(_tabId, _store);
        const auto_activation_info = _store.getState().data.rebates_auto_activation_info;
        const tab_activation_info = auto_activation_info.filter(tab_info => tab_info.tab_id === _tabId);
        if (tab_activation_info.length > 0
            && tab_activation_info[0] != undefined
            && currentWorkflowState === REBATES_WORKFLOW_STATE.ON_REBATES) {
            const tab_ready_to_activate = {"tab_id": _tabId, "is_ready_to_activate": true}
            _store.dispatch({
                type: 'UPDATE_REBATES_AUTO_ACTIVATION',
                data: tab_ready_to_activate
            });
        }
    }
}
async showRebatesHoverAndCampaignHover(tab_id, port, _store) {
    const url = port.sender.url;
    const showRebatesPopup = this.mayShowRebatesPopUp(tab_id, _store);
    if (showRebatesPopup) {
        let rzCookie = _store.getState().data.rakutenBarInfo_cookie;
        await this.checkMetaTagIfURLGoesTOPPage(url, rzCookie, _store)
        let rebates_info = this.generateRebatesInfo(url);
        _store.dispatch({
            type: 'SET_REBATES_INFO',
            data: rebates_info
        });
        //auto activate the hover
        this.autoActivateRebatesHover(rebates_info, tab_id, _store);
        this.showRebatesHover(rebates_info, port, _store);
        this.clearRebatesAutoActivationData(rebates_info, tab_id, _store);
    }
}

autoActivateRebatesHover(rebates_info, _tabId, _store) {
    const auto_activation_info = _store.getState().data.rebates_auto_activation_info;
    const tab_activation_info = auto_activation_info.filter(tab_info => tab_info.tab_id === _tabId);
    if (tab_activation_info.length > 0
        && tab_activation_info[0] != undefined
        && tab_activation_info[0].is_ready_to_activate) {
        let rebates_activated_sites = _store.getState().data.rebates_activated_sites;
        if (rebates_info.domainRegex.length > 0
            && rebates_activated_sites.filter(item => item.domain == rebates_info.domainRegex).length === 0) {
            let activated_data = {
                domain: rebates_info.domainRegex,
                is_popup: false,
                time: new Date().getTime(),
            };
            _store.dispatch({
                type: 'ADD_ACTIVATED_SITE',
                data: activated_data
            });
        }
    }
}

checkAndUpdateActivatedRebatesHover(rebates_info, _store){
    let rebates_activated_sites = _store.getState().data.rebates_activated_sites;
    let merchant_activation_info = rebates_activated_sites.filter(item => item.domain == rebates_info.domainRegex);
    let is_merchant_activated = merchant_activation_info.length > 0;
    let rebates_hover_status = new RebatesHoverStatus();
    let is_hover_pop_up = false;
    if(is_merchant_activated){
        const activated_site = merchant_activation_info[0];
        if (isActivatedPageAlreadyExpired(activated_site.time) === false) {
            // If popup is already shown before, we dont have to do anything
            if (activated_site.is_popup) {
                return rebates_hover_status;
            }
            // We will show activated popup for first time
            rebates_hover_status.is_activated = true;
            is_hover_pop_up = merchant_activation_info[0].is_popup;
        } else {
            let activated_data = {
                domain: activated_site.domain,
                is_popup: false,
                time: new Date().getTime(),
            };
            _store.dispatch({
                type: 'REMOVE_EXPIRED_ACTIVATED_SITE',
                data: activated_data
            });
        }
    }

    // once user closes NonActivated popup , we dont show it again anymore.
    if(!rebates_hover_status.is_activated){
        let closed_sites = _store.getState().data.rebates_close_popup;
        for (let site of closed_sites) {
            if (site == rebates_info.domainRegex) {
                return rebates_hover_status;
            }
        }
    }
    rebates_hover_status.is_ready_show_up = !is_merchant_activated || (is_merchant_activated && !is_hover_pop_up);
    return rebates_hover_status;
}

showRebatesHover(rebates_info, port, _store) {
    const extensionSetting = _store.getState().data.ExtensionSetting;
    if (rebates_info.domainRegex && extensionSetting &&
        (extensionSetting.BannerDisableOption == BANNER_DISABLE_OPTION.BANNER_POPUP_USE ||
            extensionSetting.BannerDisableOption == BANNER_DISABLE_OPTION.BANNER_DISABLE_SEARCH)) {
        if (rebates_info.tokenAvailable === false) {
            _store.dispatch({
                type: 'CLEAR_ACTIVATED_SITE'
            });
        }
        let rebates_hover_status = this.checkAndUpdateActivatedRebatesHover(rebates_info, _store);
        if (rebates_hover_status.is_ready_show_up) {
            /**
            * previously, there is a method get rebates hove images and campaign images together as the API provide these image URLs on the same end-point,
            * as rebates hover doesn't depends on rebates campaign, and it may confuse people,
            * I created this new method called getRebatesHoverImages
            */
            //get rebates hover image URL
            fetch(`${REBATES_TOOLBAR_CAMPAIGN_URL}`)
                .then((response) => response.json())
                .then((data) => {
                    let hoverImages = new RebatesHoverImages();
                    let json = data;
                    if (json.campaigns) {
                        const images = json.campaigns.campaign;
                        hoverImages.inactivated_image = (images.rebatespopupnonactiveimg === null || images.rebatespopupnonactiveimg === "") ? null : images.rebatespopupnonactiveimg;
                        hoverImages.activated_image = (images.rebatespopupactiveimg === null || images.rebatespopupactiveimg === "") ? null : images.rebatespopupactiveimg;
                    }

                    sendMessageByPort(port, {
                        rebates_popup: true,
                        hover_images: hoverImages,
                        rebates_info: rebates_info,
                        is_activated: rebates_hover_status.is_activated
                    });
                });
        }
        const is_user_logged_in = _store.getState().data.login == "1";
        if (is_user_logged_in && this.mayShowRebatesCampaignHover(_store, port.sender.url)) {
            fetch(`${REBATES_TOOLBAR_CAMPAIGN_URL}`)
                .then((response) => response.json())
                .then((data) => {
                    let campaignToolbar = new RebatesToolbarCampaign();
                    let json = data;
                    if (json.campaigns) {
                        const campaign = json.campaigns.campaign;
                        campaignToolbar.imageUri = (json.campaigns.campaign.imageuri === "") ? null : campaign.imageuri;
                        campaignToolbar.linkUrl = (json.campaigns.campaign.linkurl === "") ? null : campaign.linkurl;
                        campaignToolbar.pointsBackBgImage = (json.campaigns.campaign.rebatesbgimg === "") ? null : campaign.rebatesbgimg;
                        this.showupCampaignIfMeetTheRequirements(port, campaignToolbar);
                    }
                });
        }
    }
}

clearRebatesAutoActivationData(rebates_info, tab_id, _store) {
    const activated_sites = _store.getState().data.rebates_activated_sites;
    if (activated_sites.findIndex(item => item.domain == rebates_info.domainRegex) > -1) {
        _store.dispatch({
            type: 'CLEAR_REBATES_AUTO_ACTIVATION',
            data: tab_id
        });
    }
}

showupCampaignIfMeetTheRequirements(port, toolbarCampaign){
    if (toolbarCampaign.linkUrl != null) {
        //as the component level cannot control the whole campaign hover actions
        //we have to pass the valided data to the component
        let linkUrlArray = toolbarCampaign.linkUrl.split('~');
        if (linkUrlArray.length === 2) {
            toolbarCampaign.forwardUrl = `${REBATES_DOMAIN_FULL_ADDRESS}${linkUrlArray[0]}`;
            let textValue = linkUrlArray[1];
            let textArray = textValue.split(/&|=/);
            if (textArray.length >= 2) {
                toolbarCampaign.campaignInfo = textArray[1];
                toolbarCampaign.campaignSummary = textArray[3] ? textArray[3] : "";
            }
        }
        if (toolbarCampaign.forwardUrl.length > 0 && toolbarCampaign.campaignInfo.length > 0) {
            sendMessageByPort(port, {
                show_rebates_campaign_hover: true,
                toolbar_campaign: toolbarCampaign
            });
        }
    }
}
}