import { COUPON_DOMAINS, PICKUP_COUPON_TYPE, PICKUP_COUPON_PAGE_TYPE } from '../../../utilities/src/pickup_const'
import {BANNER_DISABLED_TIMER, BANNER_DISABLE_OPTION} from '../../../utilities/src/setting_config';
import CouponDataBackend from "./coupon_data_backend";
import AjaxPickup from '../factory/ajax_pickup'
import {PICKUP_CLASS_NAME} from '../../../utilities/src/pickup_const'

export default class CouponYahooAjaxPickup extends AjaxPickup {
    constructor(_domain_details) {
        super(_domain_details);
        this.domain_details = COUPON_DOMAINS.YAHOO_SHOPPING;
        this.ajax_coupon_data_backend = new CouponDataBackend();
        this.pickup_name = PICKUP_CLASS_NAME.COUPON_YAHOO_AJAX_PICKUP;
    }

    //using default implementation
    //init(_store, _request_details) { }

    is_ajax_pickup_enabled() {
        let _extension_settings = this.store.getState().data.ExtensionSetting;
        // Check the setting is ON.
        const _page_type = this.domain_details.page_type;
        let _is_this_domain_target = true;
        switch (_extension_settings.BannerDisableOption) {
            case BANNER_DISABLE_OPTION.NO_USE:
                _is_this_domain_target = false;
                break;
            case BANNER_DISABLE_OPTION.BANNER_DISABLE_SEARCH: //Search Only
                if(_page_type === PICKUP_COUPON_PAGE_TYPE.SEARCH) {
                    _is_this_domain_target =  !this.check_disabled_timer_active(_extension_settings)
                }
                break;
            case BANNER_DISABLE_OPTION.BANNER_DISABLE_ALL: //Search and Shop
                _is_this_domain_target =  !this.check_disabled_timer_active(_extension_settings)
                break;
            default:
                //case BANNER_DISABLE_OPTION.BANNER_POPUP_USE:
                break;
        }
        return _is_this_domain_target;
    }

    //using default implementation.no need for get_data as this is send service
    async get_data(_page_url) {
        if(this.store.getState().data.abTestSettings) {
            const abTestSettings = JSON.parse(this.store.getState().data.abTestSettings);
            if ((
                (this.domain_details.page_type == PICKUP_COUPON_PAGE_TYPE.SEARCH && abTestSettings.isSearchSERPPageGo == "1") ||
                (this.domain_details.page_type == PICKUP_COUPON_PAGE_TYPE.SHOP)
            )
            && abTestSettings.isCouponGo == "1"
            ) {
                let _data = await this.fetch_coupon_data(_page_url);
                if(_data != null) {
                    _data.matched_domain_details = this.domain_details;
                    return _data;
                }
            }
        }
        return null;
    }

    //using default implementation
    //reset() {}

    show_pickup_bar() {
       this.get_data(this.request_details.url).then((_data) => {
            platform_api_root.tabs.get(this.request_details.tabId, tab_info => {
                if (platform_api_root.runtime.lastError) {
                    //Tab has closed
                    return;
                }
                if (tab_info) {
                    platform_api_root.tabs.sendMessage(this.request_details.tabId, {
                        pickup_type: this.pickup_name,
                        pickup_data: _data
                    }, null);
                }
            });
        });
    }

    //Non-super functions
    async fetch_coupon_data(_page_url) {
        let responseCoupon = await this.ajax_coupon_data_backend.fetchCouponInfo(_page_url, this.domain_details.keyword);
           if (responseCoupon && responseCoupon.status === 'ok' && responseCoupon.items.length > 0) {
            const _data = {
                "items": responseCoupon.items,
                "keyword": this.store.getState().data.coupon_keyword,
                "coupon_type": PICKUP_COUPON_TYPE.COUPON_ADVANCE,
                "scid": this.domain_details.scid,
            };
            return _data;
        }
        else {
            let responseCouponRPP = await this.ajax_coupon_data_backend.fetchCouponRPPInfo(_page_url, this.domain_details.keyword);
            if (responseCouponRPP && responseCouponRPP.adList.length > 0) {
                const _data = {
                    "items": responseCouponRPP.adList,
                    "keyword": this.store.getState().data.coupon_keyword,
                    "coupon_type": PICKUP_COUPON_TYPE.RPP,
                    "scid": this.domain_details.scid,
                };
                return _data;
            }
        }
        return null;
    }

    check_disabled_timer_active(_extension_settings) {
        if(_extension_settings.PickupDisabledTimerSelectedOption === BANNER_DISABLED_TIMER.DISABLE_COMPLETELY) {
            return true;
        }
        //Disabled time option exists and current time is less than diabled time.
        else if (this.store.getState().data.pickup_banner_disabled_time != undefined &&
            this.store.getState().data.pickup_banner_disabled_time != 0 &&
            (new Date()).getTime() <= this.store.getState().data.pickup_banner_disabled_time) {
            return true;
        } else {
            //this is to take care of cases when user changes time manually.
            //This will trigger settings watcher which will reset all other things.
            return false;
        }
    }
}