// This function exists because we need to check whether the activated site is still within certain time limit.
// Every 6 hours we check with server if the activated site is still a valid site
export function isActivatedPageAlreadyExpired(time) {
    let duration = new Date().getTime() - time;
    if (duration > REBATES_ACTIVATED_DOMAIN_INTERVAL) {
        return true;
    }
    return false;
}

export class RebatesInfo {
    constructor() {
        this.link = REBATES_DOMAIN_FULL_ADDRESS;
        this.comment = '';
        this.domainRegex = '';
        this.tokenAvailable = false;
        this.fixedCashBack = true;
        this.urlName = '';
    }

    setLink(link) {
        this.link = link;
    }

    setComment(comment) {
        this.comment = comment;
    }

    setDomainRegex(regex) {
        this.domainRegex = regex;
    }

    setTokenAvailable(status){
        this.tokenAvailable = status;
    }

    setFixedCashBack(cashbackstatus){
        this.fixedCashBack = cashbackstatus;
    }
    setUrlName(urlName){
        this.urlName = urlName;
    }
}

export class RebatesCampaignHoverState {
    constructor() {
        this._domainName = '';
        this._counter = 0;
        //based on the logic this filed used as last update date, _expirationDate is confused
        this._lastUpdateTime = new Date().getTime();
    }

    get lastUpdateTime() {
        return this._lastUpdateTime;
    }

    set lastUpdateTime(value) {
        this._lastUpdateTime = value;
    }

    get domainName() {
        return this._domainName;
    }

    set domainName(value) {
        this._domainName = value;
    }

    get counter() {
        return this._counter;
    }

    set counter(value) {
        this._counter = value;
    }
}


