import {CONST_RAKUTEN_WEBSEARCH} from '../../../utilities/src/setting_config';
import { applyMiddleware } from 'redux';
import thunk from 'redux-thunk';
import reduxPersistMiddleware from '../redux_persist_middleware';

// This event is fired with the user accepts the input in the omnibox.
platform_api_root.omnibox.onInputEntered.addListener(function(text) {
    platform_api_root.tabs.update({
        "url": `${CONST_RAKUTEN_WEBSEARCH.defaultUrl}/Web?tool_id=1&ref=chext&qt=${text}`
    });
});

export default class PlatformSpecific {
    static checkTabContentScriptDueExtensionReEnabling(tab) {
    }

    static getBrowserSearchEngineSettingUrl() {
        return "chrome://settings/searchEngines";
    }

    static getBackgroundDataStoreEnhancer() {
        return applyMiddleware(reduxPersistMiddleware, thunk);
    }

    static getAjaxRequestTypes(){
        return ["xmlhttprequest"];
    }
}
