export default class DataBackend {

    constructor() {
      this.cached = {};
    }

    _getServerJson(_url) {
      if (!this.cached[_url]) {
        this.cached[_url] = new Promise((resolve, reject) => {
          fetch(_url, {credentials: "same-origin"}).then(response => {
            if (response.status === 200) {
              resolve(response.json());
            } else {
              reject(null);
            }
          }).catch(err => reject(err));
        });
      }
      return this.cached[_url];
    }

    async _fetchServerJson(_url) {
        try {
            return await this._getServerJson(_url);
        } catch (err) {
            console.error(`Error when fetching ${_url}: ${err}`);
            return null;
        }
    }

    async _fetchServerText(_url) {
        const _response = await fetch(_url, {credentials: "same-origin"}); // Call the fetch function passing the url of the API as a parameter
        const _text = await _response.text();
        return _text;
    }

    async _sendServerJson(_url, _data) {
        const _response = await fetch(_url, {
                            method: "post",
                            headers: {
                                'Accept': 'application/json',
                                'Content-Type': 'application/json'
                            },
                            //make sure to serialize your JSON body
                            body: _data
                        });
        // Call the fetch function passing the url of the API as a parameter
        const _json = await _response.json();
        return _json;
    }

    async _sendServerText(_url, _data) {
        const _response = await fetch(_url, {
                            method: "post",
                            headers: {
                                'Content-Type': 'text/plain'
                            },
                            body: _data
                        });
        // Call the fetch function passing the url of the API as a parameter
        const _text = await _response.text();
        return _text;
    }
}