import { DEFAULT_PB_ID, AFFILIATE_SETTING_OPTION } from '../../../utilities/src/affiliate_const';
import AffiliateDataBackend from './affiliate_data_backend';
import Pickup  from '../factory/pickup'
import {PICKUP_CLASS_NAME} from '../../../utilities/src/pickup_const'
export default class AffiliatePickup extends Pickup {
    constructor(_domain_details) {
        super(_domain_details);
        this.affiliate_data_backend = new AffiliateDataBackend();
        this.pickup_name = PICKUP_CLASS_NAME.AFFILIATE_PICKUP;
    }

    init(_port, _store) {
        this.port = _port;
        this.store = _store;
    }

    async get_data(_page_url) {
        const _fic_response = await this.affiliate_data_backend.doFreeItemCheck(_page_url);
        if(_fic_response.status !== "success" || _fic_response.result !== true) {
            return null;
        }
        const _ffii_response = await this.affiliate_data_backend.fetchFreeItemInfo(_page_url)
        if(_ffii_response.type !== "item") {
            //if(_ffii_response.type === "url"){ /*ignore. For Biccamera. Not supported right now*/}
            return null;
        }
        const _aic_response = await this.affiliate_data_backend.doAffiliateIDCheck();
        if(_aic_response.status !== "success") {
            return null;
        }
        const _aii_response =  await this.affiliate_data_backend.fetchAffiliateItemInfo(DEFAULT_PB_ID, _ffii_response.me_id, _ffii_response.item_id);
        if(_aii_response.status !== "success") {
            return null;
        }

        // Temporary fix for TB-21926: for some urls `link_info.alt_image_urls`
        // is returned as an object not an array.
        // TODO: Remove this fix when the issue is resolved on the server-side.
        if (!Array.isArray(_aii_response.link_info.alt_image_urls)) {
            if (_aii_response.link_info.alt_image_urls !== null &&
                typeof _aii_response.link_info.alt_image_urls == "object") {
              _aii_response.link_info.alt_image_urls =
                  Object.values(_aii_response.link_info.alt_image_urls);
            } else {
              // It is something else than an object so reset it to an empty
              // array just to be safe.
              _aii_response.link_info.alt_image_urls = [];
            }
        }

        const _data = {
                "me_id":_ffii_response.me_id,
                "item_id": _ffii_response.item_id,
                "pb_id": DEFAULT_PB_ID,
                "link_info": _aii_response.link_info,
                "hybrid_link": _aii_response.hybrid_link,
                "tu_id": this.store.getState().data.tbuid
            }
        return _data;
    }

    is_pickup_enabled() {
        let _extension_settings = this.store.getState().data.ExtensionSetting;
        const _enabled = _extension_settings &&
                    _extension_settings.AffiliateOption == AFFILIATE_SETTING_OPTION.SHOW_BAR.RADIO_INDEX;
        return _enabled;
    }

    //using default implementation
    //show_pickup_bar() { }
}