import { createStore } from 'redux';
import { wrapStore } from 'react-chrome-redux';
import rootReducer from './reducers';
import PlatformSpecific from 'PlatformSpecific';
import ApplicationStateStorage from './application_state_storage'
import { globalState } from './globalState'

export const store = createStore(rootReducer, {}, PlatformSpecific.getBackgroundDataStoreEnhancer());

async function loadDataFromStorage() {
    await ApplicationStateStorage.load();
    //console.log('Loaded data from storage!');
    globalState.isDataLoadedFromStorage = true;
}

void loadDataFromStorage();

wrapStore(store, {
    portName: 'background_data'
});

// Function to defer the logic until the data is loaded in store (because StorageAPI is now async)
export const waitForStoreToBeLoaded = async() => {
    return new Promise((resolve) => {
        if (globalState.isDataLoadedFromStorage === true) {
            resolve(true);
            return;
        }

        platform_api_root.alarms.create("StorageInterval", {
            periodInMinutes: 0.002
        });
        platform_api_root.alarms.onAlarm.addListener((alarm) => {
            if (alarm.name === "StorageInterval") {
                // Check the loaded flag
                if (globalState.isDataLoadedFromStorage === true) {
                    platform_api_root.alarms.clear("StorageInterval");
                    resolve(true);
                }
            } else {
                resolve(true);
            }
        });
    });
}
