import { EXTENSION_SETTING, SEARCHBAR_SETTING } from '../../utilities/src/setting_config';
import {REBATE_ADDITIONAL_DISCOUNT_TARGET_INFO} from '../../utilities/src/rebates_const';
import { store } from './background_data_store';

/////////////////////////////////////////////////////////////////////////////
// Load/save application state from/to browser local storage.
//

async function save_all_storage_api(obj) {
  return new Promise((resolve) => {
    platform_api_root.storage.local.set(obj, () => {
        resolve();
    });
  });
}

async function load_storage_api(key, value) {
  return new Promise((resolve) => {
    platform_api_root.storage.local.get(key, (result) => {
      let output;
      if (result[key] !== undefined) {
        output = result[key];
        if(typeof output === 'string') {
          try {
            // only for setting stuff will come here
            output = JSON.parse(output);
          } catch (e) {
          }
        }
      } else {
        output = value;
      }
      store.dispatch({ type: 'SET_KEY_WITH_DATA', data: {key: key, value: output} });
      resolve();
    });
  });
}

async function load() {
  let stateobj = {};

  let loadState = async(varname, handler_for_undefined) => {
    let value = undefined;
    if (handler_for_undefined) {
        value = handler_for_undefined();
    }

    await load_storage_api(varname, value);
  };

  let loadSettingState = async function(setting_name, setting_default_class) {
    let setting_name_captured = setting_name;
    let setting_default_class_captured = setting_default_class;

    await loadState(setting_name, () => new setting_default_class_captured(),
      value => {
        let valueobj = null;
        try {
          if(typeof value === 'string') {
            valueobj = JSON.parse(value);
          } else {
            valueobj = value;
          }
        }
        catch(e) {
          console.error("Parse error: " + setting_name_captured + " " + e);
          return new setting_default_class_captured();
        }

        // Convert old value format: in previous versions we had additional level
        // of object indirection which is removed now.
        if (!Object.prototype.hasOwnProperty.call(valueobj, setting_name_captured)) {
          return valueobj;
        }

        // Ensure that it was not "corrupted" after conversion due bugs in code:
        // there must be no other properties except of the indirection object
        // property.
        if (Object.keys(valueobj).length > 1) {
          console.error("Format error in " + setting_name_captured);
        } else {
          // For safety copy only "known" properties - i.e. which are defined in
          // the default object.
          let extensionSetting = valueobj[setting_name_captured];
          let defaultSetting = new setting_default_class_captured();
          Object.keys(defaultSetting).forEach(function(keyname) {
            valueobj[keyname] = (Object.prototype.hasOwnProperty.call(extensionSetting, keyname))
                                  ? extensionSetting[keyname]
                                  : defaultSetting[keyname];
          });
        }
        delete valueobj[setting_name_captured];
        return valueobj;
      });
  };

  await Promise.allSettled([
    loadState("history", () => [], // empty array if undefined.
        value => {  // parse the defined string to an array.
          return value != "" ? value.split(',') : [];
        }),
    loadState("uuid"),
    loadState("tbuid"),
    loadState("clearHistory", () => "0"),
    loadState("rakutenBarInfo_active"),
    loadState("rakutenBarInfo_close_popup"),
    loadState("rakutenBarInfo_send_number"),
    loadState("rakutenBarInfo_accessurl"),
    loadState("rakutenBarInfo_domain"),
    loadState("rakutenBarInfoConfig_update_time"),
    loadState("rakutenBarInfo_userAgent"),
    loadState("rakutenBarInfo_osVersion"),
    loadState("rakutenBarInfo_cookie"),
    loadState("URLCheck"),
    loadState("isRakutenAsDefault"),
    loadState("messageReadTime"),
    loadState("yamawakeInfo"),
    loadState("kuchisuCount"),
    loadState("loginCheckTime"),
    loadState("login"),
    loadState("callLoad"),
    loadState("cid"),
    loadState("rebates_campaign_hover_states", () => [],
        value => {
          return JSON.parse(value);
        }),
    loadSettingState("ExtensionSetting", EXTENSION_SETTING),
    loadSettingState("SearchBarSetting", SEARCHBAR_SETTING),
    loadState("pickup_banner_disabled_time"),
    loadSettingState("rebates_additional_discount_target_info", REBATE_ADDITIONAL_DISCOUNT_TARGET_INFO),
  ]);

  return stateobj;
}

/////////////////////////////////////////////////////////////////////////////
// Public interface
//
export default {
  load: load,
  saveAll: save_all_storage_api,
}
