import { COUPON_DOMAINS, ICHIBA_DOMAINS} from '../../../utilities/src/pickup_const'
import { AFFILIATE_DOMAINS } from '../../../utilities/src/affiliate_const'

import AffiliatePickup from '../affiliate/affiliate_pickup';
import CouponPickup from '../coupon/coupon_pickup';
import IchibaPickup from '../ichiba/ichiba_pickup';

export default class PickupFactory {
    getPickupInstance(_page_url) {
        let _domain_details = this.isAffiliatePickup(_page_url);
        if(_domain_details !== null) {
            return new AffiliatePickup(_domain_details);
        }
        _domain_details = this.isCouponPickup(_page_url);
        if(_domain_details !== null) {
            return new CouponPickup(_domain_details);
        }
        _domain_details = this.isIchibaPickup(_page_url);
        if(_domain_details !== null) {
            return new IchibaPickup(_domain_details);
        }
        return null;
    }

    isAffiliatePickup(_page_url) {
        for (let _domain in AFFILIATE_DOMAINS) {
            let _domain_details = AFFILIATE_DOMAINS[_domain];
            if(_page_url.search(_domain_details.domain_pattern) !== -1){
                return _domain_details;
            }
        }
        return null;
    }

    isCouponPickup(_page_url) {
        for (let _domain in COUPON_DOMAINS) {
            let _domain_details = COUPON_DOMAINS[_domain];
            if(_page_url.search(_domain_details.domain_pattern) !== -1){
                return _domain_details;
            }
        }
        return null;
    }

    isIchibaPickup(_page_url) {
        for (let _domain in ICHIBA_DOMAINS) {
            let _domain_details = ICHIBA_DOMAINS[_domain];
            if(_page_url.search(_domain_details.domain_pattern) !== -1){
                return _domain_details;
            }
        }
        return null;
    }
}