import {store} from '../background_data_store';
import {
        PICKUP_BAR_COUPON_URL,
        PICKUP_BAR_COUPON_SID,
        PICKUP_BAR_RPP_URL
    } from '../../../utilities/src/pickup_const';
import { getURLParam } from '../../../utilities/src/common_helper';
import DataBackend from '../factory/data_backend'
export default class CouponDataBackend extends DataBackend {
    constructor() {
        super();
    }

    //Using default implementation
    //async _fetchServerJson(_url) { }

    async fetchCouponInfo(_page_url, _keyword_param) {
        const _keyword_value = getURLParam(_page_url, _keyword_param);
        store.dispatch({
            type: 'SET_COUPON_KEYWORD',
            data: decodeURIComponent(_keyword_value)
        });
        if (_keyword_value != null) {
            let _url = PICKUP_BAR_COUPON_URL + _keyword_value + PICKUP_BAR_COUPON_SID;
            return this._fetchServerJson(_url);
        }
        return null;
    }

    async fetchCouponRPPInfo(_page_url, _keyword_param) {
        const _keyword_value = getURLParam(_page_url, _keyword_param);
        store.dispatch({
            type: 'SET_COUPON_KEYWORD',
            data: decodeURIComponent(_keyword_value)
        });
        if (_keyword_value != null) {
            let _url = PICKUP_BAR_RPP_URL + _keyword_value;
            return this._fetchServerJson(_url);
        }
        return null;
    }
}