// Content Script
platform_api_root.runtime.onMessage.addListener(function(request, sender, sendResponse) {
    // 
    // 

    //console.log('New request for service worker: ' + JSON.stringify(request));

    let date = new Date();
    if (request.method == "default") {
        store.dispatch({ type: 'SET_RAKUTEN_ASDEFAULT', data: request.value });
    }
    else if (request.method == "frameSelection") {
        platform_api_root.tabs.query({ "active": true, "lastFocusedWindow": true }, function(activetabs) {
            if (activetabs.length > 0) {
                let currenttab = activetabs[0];
                platform_api_root.tabs.sendMessage(currenttab.id, {
                    "method": "displayselectedToBox", "value": request.data
                }, null);
            }
        });
    }

    // 
    else if (request.method == "checkLogin") {
        RTBbg.getRtbSess();
    }

    // xml/user
    else if (request.method == "checkLoginOnly") {
        RTBbg.checkLogin();
        RTBbg.getTgmCount();
    }

    // 
    else if (request.method == "checkLoginTimer") {
        date = new Date();
        if (!store.getState().data.loginCheckTime
            || date.getTime() - store.getState().data.loginCheckTime > RTBbg.loginCheckTime) {
            RTBbg.checkLogin();
        }

        // we dont want to send request again and again so utilize this part to check the url...
        let urlMatch = /^(http|https):\/\/toolbar.rakuten.co.jp\/intro\/install\/platform_identifier\/update.html/i;
        if (urlMatch.test(sender.url)) {
            platform_api_root.scripting.executeScript(sender.tab.id, { file: 'js/options_bridge.js' });
        }
    }

    // 
    else if (request.method == "loaded") {
        // 
        RTBbg.updateIconInAllTabs();

        // 30
        const date = new Date();
        if (!store.getState().data.messageReadTime
            || (date.getTime() - store.getState().data.messageReadTime) > RTBbg.messageReadTime) {
            RTBbg.getTgmCount();
        }
    }

    // 
    else if (request.method == "deleteSpFlag") {
        store.dispatch({ type: 'CLEAR_RAKUTEN_ASDEFAULT' });
        RTBbg.checkLogin();
    }

    // 
    else if (request.method == "messageListUpdate") {
        RTBbg.getTgmCount();
    }

    // chrome://chrome/settings/searchEngines
    else if (request.method == "opensetting") {
        platform_api_root.tabs.create({
            url: `${CONST_RAKUTEN_WEBSEARCH.defaultUrl}/`
        });

        let SETTINGS_URL = PlatformSpecific.getBrowserSearchEngineSettingUrl();

        let settingsWindowId;
        let settingsTabId;

        platform_api_root.windows.create({
            url: SETTINGS_URL
        }, function(window) {
            settingsWindowId = window.id;
            for (let i = 0; i < window.tabs.length; i++) {
                if (window.tabs[i].url == SETTINGS_URL) {
                    settingsTabId = window.tabs[i].id;
                    break;
                }
            }

            if (settingsTabId) {
                platform_api_root.windows.getCurrent(function(win) {
                    if (win.id == settingsWindowId) {
                        platform_api_root.tabs.update(settingsTabId, {
                            active: true
                        });
                        platform_api_root.tabs.reload(settingsTabId);
                    }
                });
            }
        });
    }

    else if (request.method == "getTabSearchBarStatus") {
        let result = true;
        if (sender.tab) {
            result = TabController.getSearchBarStatus(sender.tab.id);
            sendResponse({ status: result });
        }
    }
    else if (request.method == "setTabSearchBarStatus") {
        if (sender.tab) {
            TabController.updateSearchBarStatus(sender.tab.id, request.value);
        }
    }
    // SERP
    else if (request.method == "getRtbSess") {
        RTBbg.getRtbSess();
    }
    else if (request.method == "clearServerHistory") {
        platform_api_root.tabs.query({ "active": true, "currentWindow": true }, function(activetabs) {
            if (activetabs && activetabs.length > 0) {
                let currentTab = activetabs[0];
                RTBbg.clearSerpHistory(currentTab.id);
            }
        });
    }
    else if(request.method == "getRebatesCampaignList"){
        sendResponse(store.getState().data.rebates_campaignInfo);
    }
    else if (request.method == "createTab") {
        platform_api_root.tabs.create({
            active: true,
            url: request.url
        });
    }
    else if (request.method == "checkBackgroundAlive") {
        sendResponse(true);
    }
    else if (request.method == "revertPopupSettings") {
        RTBbg.setRevertPopupSetting();
    }
});

