platform_api_root.runtime.onInstalled.addListener(async function(details) {
    await waitForStoreToBeLoaded();
    if (details.reason == "install") {
        RTBbg.openPostInstallPage();
    } else if (details.reason == "update") {
        //We check for newly added property. If it does not exist, we update Rebate Options value
        //This property was added in release version 4.629
        if (!("PickupDisabledTimerSelectedOption" in store.getState().data.ExtensionSetting)) {
            migrateRebatesOptionsSettings();
        }
        if ("RebatesOption" in store.getState().data.ExtensionSetting) {
            mapRebatesOptionToBanner();
        }
    }
});


function mapRebatesOptionToBanner() {
    let extension_setting = store.getState().data.ExtensionSetting;
    if (!("BannerDisableOption" in store.getState().data.ExtensionSetting)) {
        extension_setting.BannerDisableOption = extension_setting.RebatesOption;
    }
    delete extension_setting.RebatesOption;
    store.dispatch({ type: 'SET_EXTENSION_SETTING', data: extension_setting });
}

function migrateRebatesOptionsSettings() {
    let extension_setting = store.getState().data.ExtensionSetting;
    const oldBannerDisableOption = extension_setting.RebatesOption;
    switch (oldBannerDisableOption) {
        case 0: // BANNER_POPUP_USE
            //No Change in this case
            extension_setting.PickupDisabledTimerSelectedOption = null;
            break;
        case 1: // BANNER_DISABLE_ALL
            extension_setting.BannerDisableOption = BANNER_DISABLE_OPTION.BANNER_DISABLE_ALL;
            extension_setting.PickupDisabledTimerSelectedOption = BANNER_DISABLED_TIMER.DISABLE_COMPLETELY;
            break;
        case 2: // NO_USE
            extension_setting.BannerDisableOption = BANNER_DISABLE_OPTION.NO_USE;
            extension_setting.PickupDisabledTimerSelectedOption = null;
            break;
        default:
    }
    store.dispatch({ type: 'SET_EXTENSION_SETTING', data: extension_setting });
}

