let extensionSettingWatcher = watch(store.getState, 'data.' + CONST_EXTENSION_SETTING);
let loginSettingWatcher = watch(store.getState, 'data.' + CONST_LOGIN_SETTING);


store.subscribe(loginSettingWatcher((newVal, oldVal, objectPath) => {
    if (oldVal == undefined || newVal == undefined) {
        return;
    }
    if (oldVal != newVal) {
        RTBbg.rebates.rebatesLoginStatusChanged(newVal, oldVal, store);
        if(newVal == 0 && oldVal == 1) { //Logging Out
            RTBbg.setRevertPopupSetting();
            RTBbg.clearRebatesHoverData();
        }
    }
}));

store.subscribe(extensionSettingWatcher((newVal, oldVal, objectPath) => {
    if (oldVal === undefined || newVal == undefined) {
        return;
    }

    if (oldVal.IsSearchHistoryStored != newVal.IsSearchHistoryStored) {
        TabController.enumTabs(function(tabId) {
            RTBbg.reloadTopPage(tabId);
        });
    }

    if (oldVal.BannerDisableOption != newVal.BannerDisableOption ||
        oldVal.PickupDisabledTimerSelectedOption != newVal.PickupDisabledTimerSelectedOption) {
        clearTimeout(global.pickup_disabled_timer);
        let disabled_time = null;
        switch (newVal.PickupDisabledTimerSelectedOption) {
            case BANNER_DISABLED_TIMER.TIMER_24HOURS:
                disabled_time = (new Date()).getTime() + REBATES_SEARCH_POPUP_DISABLED_24HOURS;
                setLongTimeout(
                    function(){ RTBbg.setRevertPopupSetting(); },
                    REBATES_SEARCH_POPUP_DISABLED_24HOURS,
                    global.pickup_disabled_timer
                    );
                    store.dispatch({ type: 'SET_PICKUP_BANNER_DISABLED_TIME', data: disabled_time });
                break;
            case BANNER_DISABLED_TIMER.TIMER_30DAYS:
                disabled_time = (new Date()).getTime() + REBATES_SEARCH_POPUP_DISABLED_30DAYS;
                setLongTimeout(
                        function(){ RTBbg.setRevertPopupSetting(); },
                        REBATES_SEARCH_POPUP_DISABLED_30DAYS,
                        global.pickup_disabled_timer
                        );
                store.dispatch({ type: 'SET_PICKUP_BANNER_DISABLED_TIME', data: disabled_time });
                break;
            default: //BANNER_DISABLED_TIMER.DISABLE_COMPLETELY or  null
                store.dispatch({ type: 'CLEAR_PICKUP_BANNER_DISABLED_TIME' });
                break;
        }
    }
}));

