import ApplicationStateStorage from './application_state_storage';
import reducer from './reducers';

import { globalState } from './globalState'

const reduxPersistMiddleware = store => next => action => {
    const state = store.getState();
    const nextState = reducer(state, action);

    // Save the state to Extension Storage API after every dispatch
    // We don't need to trigger save for 'SET_KEY_WITH_DATA' actions because it's in the loading behavior
    // globalState..isDataLoadedFromStorage is used to prevent saving data before it is loaded
    if (globalState.isDataLoadedFromStorage && action.type && action.type !== 'SET_KEY_WITH_DATA') {
        ApplicationStateStorage.saveAll(nextState.data).then(() => {
            //console.log('Saved data to storage, triggered by action: ' + action.type);
        });
    }

    next(action);
}

export default reduxPersistMiddleware;
