import { AMAZON_ASSISTANT_IFRAME_FILTER} from './common_const'
import {validateRuntimeOnUserInteraction} from './runtime_helper';

export const DISCONNECTED_PORT_NAME = 'DISCONNECTED_PORT';
//Wrapper function to enable calling setTimeout beyong it's max limit
export function setLongTimeout(callback, timeout_ms, timeoutHandle){
    const maxDelay = Math.pow(2, 31)-1;
     //if we have to wait more than max time, need to recursively call this function again
    if(timeout_ms > maxDelay) {
        //now wait until the max wait time passes then call this function again with
        //requested wait - max wait we just did, make sure and pass callback
        timeoutHandle = setTimeout(
            function(){
                setLongTimeout(callback, (timeout_ms - maxDelay), timeoutHandle);
            },
            maxDelay);
        }
    else {
        //if we are asking to wait less than max, finally just do regular setTimeout and call callback
      timeoutHandle = setTimeout(callback, timeout_ms);
    }
}

export function getURLParam(_url_to_parse, _search_param){
    const _url = require('url');
    let current_url = _url.parse(_url_to_parse);
    let search_params = new URLSearchParams(current_url.search);
    return search_params.get(_search_param);
}

export function parseURL(_url_to_parse){
    const _url = require('url');
    return _url.parse(_url_to_parse);
}

export function findMatchPattern(_url, _patterns) {
    let got_pattern_match = false;

    for (let i in _patterns) {
        let r = new RegExp(_patterns[i]);
        got_pattern_match = r.test(_url);
        if (got_pattern_match) {
            break;
        }
    }
    return got_pattern_match;
}

export function sendMessageByPort(port, message) {
  try {
    port.onDisconnect.addListener(_port => {
        _port.name = DISCONNECTED_PORT_NAME;
    });
    if (port.name != DISCONNECTED_PORT_NAME) {
        if(port.sender && port.sender.tab){
            const tab_id = port.sender.tab.id ? port.sender.tab.id : 0;
            platform_api_root.tabs.get(tab_id, () => {
                if(!platform_api_root.runtime.lastError){
                    port.postMessage(message);
                }
            });
        }else{
            port.postMessage(message);
        }

    }
  } catch(e) {
    console.log('sendMessageByPort failed:', e);
  }
}

export function handleMutationCallback(_mutation_list, _amazon_assistant_flag, _callback_method, _callback_args = null) {
    const relevant_mutations = _mutation_list.filter((_mutation) => (_amazon_assistant_flag?_mutation.addedNodes.length:_mutation.removedNodes.length) > 0);
    relevant_mutations.forEach(function(mutation) {
        const relevant_nodes = Array.from((_amazon_assistant_flag?mutation.addedNodes:mutation.removedNodes)).filter((node) => (node instanceof HTMLElement) && node.matches(AMAZON_ASSISTANT_IFRAME_FILTER));
        if(relevant_nodes.length > 0) {
            const node = relevant_nodes[0];
            const node_height = node.offsetHeight>0?node.offsetHeight:parseInt(node.style.height, 10);
            if(node_height < 90) {
                if(_amazon_assistant_flag) {
                    node.addEventListener('load', () => {
                        _callback_method({"isAmazonAssistantVisible": _amazon_assistant_flag}, _callback_args);
                    });
                }
                else {
                    _callback_method({"isAmazonAssistantVisible": _amazon_assistant_flag}, _callback_args);
                }
            }
        }
    });
}

//We add margin-top to page to move the contents of the page down to make space for our affiliate bar. The fixed elements stay on their location and get hidden
// or displaced inconsistently. We transform all fixed elements which are within the range of our bar's height and visible to render them correctly.
export function handleFixedElements(_frame, _base_height, _additional_height){
    try {
        let fixedElementsOld = document.body.getElementsByClassName("fixed_elem_fix")
        while (fixedElementsOld.length) {
            fixedElementsOld[0].classList.remove('fixed_elem_fix');
        }

        //Add Transition Effect to Adding Affiliate Bar
        let _pageRoot = document.documentElement;
        _pageRoot.style.setProperty('margin-top', (_base_height + _additional_height) + "px", 'important');
        _frame.style.top = _additional_height + "px";
        //Get List of all Elements in the host page which are fixed.
        const fixedElements = Array.prototype.slice.call(document.body.getElementsByTagName("*"), 0).filter(x => getComputedStyle(x, null).getPropertyValue("position") === "fixed");
        let fixedElemList = [];
        //This function calculates css path to reach the element starting from top
        let cssPath = function(el) {
            let path = [];
            while (el.nodeType === Node.ELEMENT_NODE) {
                let selector = el.nodeName.toLowerCase();
                if (el.id) {
                    selector += '#' + el.id;
                } else if (el.tagName.toLowerCase() != "body" &&
                            el.tagName.toLowerCase() != "html") {
                    let sib = el, nth = 0;
                    while (sib.nodeType === Node.ELEMENT_NODE && (sib = sib.previousSibling)) {
                        nth++;
                    }
                    selector += ":nth-child("+nth+")";
                }
                path.unshift(selector);
                el = el.parentNode;
            }
            return path.join(" > ");
        }
        //For all fixed elements meeting our criteria attach the new class
        fixedElements.forEach(fe => {
            if(fe.tagName.toLowerCase() === "iframe") {
                return;
            }
            const rect = fe.getBoundingClientRect()
            const distanceFromWindowTop = rect.top
            if((distanceFromWindowTop < _base_height) && !!(fe.offsetWidth || fe.offsetHeight || fe.getClientRects().length)) {
                fe.classList.add("fixed_elem_fix");
                if((fe instanceof Element) && fe.id != _frame.id) {
                    fixedElemList.push(cssPath(fe) + ".fixed_elem_fix");
                }
            }
        });
        //Add the new style
        let sheet = document.createElement('style')
        sheet.innerHTML = fixedElemList.join(",") + "{transform: translateY("+ (_base_height + _additional_height) +"px) !important;}";
        document.body.appendChild(sheet);
        }
    catch(ex){
        console.log(ex, ex.stack);
        console.log("Error in Fixing position:fixed elements. Some page elements might be hidden behind the affiliate bar.");
    }
}

export function handleGeneralBarItemClick(e, google_analytics_value) {
  e.preventDefault();
  // Save `href` before checking the runtime as the event object will be lost
  // by the time the callback is executed.
  const href = e.currentTarget.href;
  validateRuntimeOnUserInteraction(() => {
    platform_api_root.runtime.sendMessage({
        method: "createTab",
        url: href
    });
  });
}
