import { YAHOO_AJAX_DOMAINS } from '../../../utilities/src/ajax_handler_const'
import CouponYahooAjaxPickup from '../coupon/coupon_yahoo_ajax_pickup';

export default class AjaxPickupFactory {
    getAjaxPickupInstance(_page_url) {
        let _domain_details = this.isYahooAjaxPickup(_page_url);
        if(_domain_details !== null) {
            return new CouponYahooAjaxPickup(_domain_details);
        }
        return null;
    }

    isYahooAjaxPickup(_page_url) {
        for (let _domain in YAHOO_AJAX_DOMAINS) {
            let _domain_details = YAHOO_AJAX_DOMAINS[_domain];
            if(_page_url.search(_domain_details.domain_pattern) !== -1){
                return _domain_details;
            }
        }
        return null;
    }
}