import {
    AFFILIATE_BAR_AFFILIATE_LINK_URL,
    AFFILIATE_BAR_FREE_ITEM_CHECK,
    AFFILIATE_BAR_GET_FREE_ITEM_INFO,
    AFFILIATE_BAR_AFFILIATE_ID_CHECK
    } from '../../../utilities/src/affiliate_const';
import DataBackend from '../factory/data_backend'
export default class AffiliateDataBackend extends DataBackend {
    constructor() {super();}

    async fetchAffiliateItemInfo(_pb_id, _me_id, _item_id) {
        const _afl_link_url = AFFILIATE_BAR_AFFILIATE_LINK_URL + "?pb_id=" + _pb_id +
        "&me_id=" + _me_id + "&item_id=" + _item_id + "&resizable=1";
        return await this._fetchServerJson(_afl_link_url);
    }

    async fetchFreeItemInfo(_page_url) {
        const afl_item_info_url = AFFILIATE_BAR_GET_FREE_ITEM_INFO + "?u=" + _page_url;
        return await this._fetchServerJson(afl_item_info_url);
    }

    async doFreeItemCheck(_page_url) {
        const _afl_check_url = AFFILIATE_BAR_FREE_ITEM_CHECK + "?u=" + _page_url;
        return await this._fetchServerJson(_afl_check_url);
    }

    async doAffiliateIDCheck() {
        return await this._fetchServerJson(AFFILIATE_BAR_AFFILIATE_ID_CHECK);
    }
}