import {sendMessageByPort} from '../../../utilities/src/common_helper';
import { BANNER_DISABLED_TIMER, BANNER_DISABLE_OPTION } from '../../../utilities/src/setting_config';

export default class Pickup {
    constructor(_domain_details) {
        this.domain_details = _domain_details;
    }

    init(_port, _store) {
        this.port = _port;
        this.store = _store;
    }

    is_pickup_enabled() {
        return false;
    }

    check_overlapping_pickups() {
        return false;
    }

    async get_data(_page_url) {
        return null;
    }

    reset() {
        const pickup_banner_disabled_time = this.store.getState().data.pickup_banner_disabled_time;
        if (pickup_banner_disabled_time &&
            pickup_banner_disabled_time <= (new Date()).getTime()){
            let _extension_settings = this.store.getState().data.ExtensionSetting;
            if(_extension_settings.PickupDisabledTimerSelectedOption != BANNER_DISABLED_TIMER.DISABLE_COMPLETELY){
                _extension_settings.PickupDisabledTimerSelectedOption = null;
                _extension_settings.BannerDisableOption = BANNER_DISABLE_OPTION.BANNER_POPUP_USE;
                this.store.dispatch({ type: 'SET_EXTENSION_SETTING', data: _extension_settings });
                this.store.dispatch({ type: 'CLEAR_PICKUP_BANNER_DISABLED_TIME' });
                clearTimeout(global.pickup_disabled_timer);
            }
        }
    }

    show_pickup_bar(_page_url) {
        this.get_data(_page_url).then((_data) => {
            if(_data != null) {
                sendMessageByPort(this.port, {
                                pickup_type: this.pickup_name,
                                pickup_data: _data
                                }
                );
            }
        });
    }
}